@import url(https://fonts.googleapis.com/css?family=Raleway:300,400,600);
body {
  font-family: "Raleway", sans-serif;
  color: #1d272c;
  font-size: 0.9rem;
  line-height: 1.6;
}

.btn-line {
  background-color: transparent;
  box-shadow: inset 0 0 0 1px #dee2e6, inset 0 0 0 2px transparent, inset 0 0 0 3px transparent;
  color: #1d272c;
}

.btn-line:hover {
  background-color: #0084bf;
  color: #fff;
}

.text-area {
  min-height: 200px;
  resize: vertical;
}

.ta-sh {
  min-height: 100px;
}

.btn {
  border-radius: 2px;
}

.navbar-laravel {
  background-color: #343a40;
}

.navbar-laravel .nav-link {
  color: rgba(255, 255, 255, 0.5);
}

.navbar-laravel .nav-link:hover {
  color: #ffffff;
}

.navbar-laravel .nav-item.active .nav-link {
  color: #ffffff;
  font-weight: bold;
}

.navbar-laravel .navbar-brand {
  color: red;
}

.page-link {
  border: 1px solid transparent;
  border-radius: 2px;
  background-color: transparent;
}

.page-link:hover {
  border-color: #e4e4e4;
  background-color: transparent;
}

.page-item {
  margin: 0 2px;
}

.page-item.active .page-link {
  background-color: #0084bf;
  border-color: #0084bf;
}

.page-item.disabled .page-link {
  border-color: transparent;
  cursor: not-allowed;
}

.posts__item {
  display: flex;
  flex-direction: column;
  margin-bottom: 30px;
}

.posts__item-wrap {
  height: 100%;
  display: flex;
  flex-direction: column;
  box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.1), 0 1px 1px rgba(0, 0, 0, 0.1);
  position: relative;
}

.posts__img-wrap {
  width: 100%;
  padding-bottom: 60%;
  background-color: #e4e4e4;
  position: relative;
}

.posts__img-wrap .btn {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: none;
  z-index: 1;
}

.posts__img-wrap .btn:hover {
  background: aliceblue;
}

.posts__img-wrap:hover:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.2);
}

.posts__img-wrap:hover .btn {
  display: block;
}

.posts__title, .posts__overview {
  padding: 10px;
}

.posts__pagination .pagination {
  justify-content: flex-end;
  margin-bottom: 0;
}

.posts__bar {
  margin-bottom: 30px;
}

.btn-approve {
  width: 100%;
  color: #0084bf;
  background-color: transparent;
  opacity: 0.8;
  border-radius: 0;
  border-top: 1px solid #e4e4e4;
}

.btn-approve:hover {
  opacity: 1;
  background: aliceblue;
}

/*------------------------------------------------------------------------------------------------*/
.pd__bar-right .btn {
  float: right;
}

.pd__info {
  color: gray;
  margin-top: 20px;
}

.pd__info span {
  display: block;
  margin-right: 10px;
}

.pd__main {
  margin-top: 30px;
}

.pd__conent {
  margin-top: 10px;
  opacity: 0;
  transition: opacity ease-out 0.2s;
}

.pd__conent.active {
  opacity: 1;
}

.pc__bar {
  margin-bottom: 30px;
}

.pc__preview {
  margin-top: 10px;
  padding: 10px;
  border-radius: 2px;
  border: 1px solid #e4e4e4;
}

.pc__preview h4 {
  font-size: 20px;
  margin-left: -10px;
  margin-right: -10px;
  padding: 0 10px 10px;
  border-bottom: 1px solid #e4e4e4;
}

@media only screen and (max-width: 768px) {
  .posts__pagination .pagination {
    justify-content: flex-start;
  }
  .pd__save {
    justify-content: flex-start;
  }
}

.pv__title {
  font-size: 18px;
  font-weight: bold;
}

.pv__sub i {
  margin-right: 6px;
}

.pv .modal-content {
  min-height: 300px;
  position: relative;
}

.pv__loading {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #fff;
}

.pv__loading .fa {
  font-size: 30px;
}
